<template>
    <div class="jsplumb">
        <div class="picture picture1">
            <div id="item_left" class="item"></div>
            <div id="item_right" class="item" style="left:150px;"></div>
        </div>
        <div class="picture picture2">
            <div id="node_1" class="item"></div>
            <div id="node_2" class="item" style="left:150px;"></div>
        </div>
    </div>
</template>
<script type="text/javascript">
    export default{
        name: 'jsplumb',
        data() {
            return {
            }
        },
        methods: {
            init() {
                /** DESC
                    Anchors：锚点类型
                        · 静态锚点
                        · 动态锚点
                        · 边缘锚点
                        · 固定锚点
                    Connectors：连线类型
                        · Bezier 贝赛尔曲线(默认)
                        · Straight 直线
                        · Flowchart 90度转角线
                        · State Machine 状态机
                    Endpoints：端点类型
                        · Dot 圆点
                        · Rectangle 矩形
                        · Image 图像
                        · Blank 空白
                    Overlays：连接线上的文字或者箭头之类
                        · Arrow
                        · Label
                        · PlainArrow
                        · Diamond
                        · Custom
                 */
                jsPlumb.ready(function () {
                    let common = {
                        endpoint: 'Dot',
                        connector: ['Bezier'],
                        anchor: ['Left', 'Right', 'Top'],
                        paintStyle: { stroke: 'lightgray', strokeWidth: 2 },
                        endpointStyle: { fill: 'lightgreen', outelineStroke: 'darkred', outlineWidth: 10 }
                    };
                    let nodeJson = [
                        {
                            startId: 'item_left',
                            endId: 'item_right'
                        },
                        {
                            startId: 'item_right',
                            endId: 'node_1'
                        },
                        {
                            startId: 'node_1',
                            endId: 'node_2'
                        }
                    ];
                    nodeJson.map((node, idx)=>{
                        jsPlumb.connect({
                            source: node.startId, // Source 源节点
                            target: node.endId // Target 目标节点
                        }, common)
                        if(idx == 0) {
                            jsPlumb.draggable(node.startId);
                            jsPlumb.draggable(node.endId);
                        } else {
                            jsPlumb.draggable(node.endId);
                        }
                    })
                })
            }
        },
        mounted() {
            this.init();
        }
    }
</script>
<style type="text/css" scoped>
    /*#diagramContainer {
      padding: 20px;
      width: 80%;
      height: 200px;
      border: 1px solid gray;
    }

    .item {
      height: 80px;
      width: 80px;
      border: 1px solid blue;
      float: left;
    }*/
    .jsplumb {
        position: relative;
    }
    .picture {
        height: 200px;
    }
    .item {
        height: 80px;
        width: 80px;
        border: 1px solid blue;
        position: absolute;
    }
</style>


