import Vue from 'vue'
import Router from 'vue-router'
import slider from '@/page/slider'
import index from '@/page/index'
import watch from '@/page/watch'
import align from '@/page/align'
import elform from '@/page/elform'
import jsplumb from '@/page/jsplumb'

import test from '@/page/test'
Vue.component('test', test)

Vue.use(Router)

export default new Router({
  routes: [
  	{
      	path: '/',
      	name: 'index',
      	component: index,
        children: [
          {
            path: '/slider',
            name: 'slider',
            component: slider
          },
          {
            path: '/watch',
            name: 'watch',
            component: watch
          },
          {
            path: '/align',
            name: 'alignWays',
            component: align
          },
          {
            path: '/elform',
            name: 'elmentUI-form',
            component: elform
          },
          {
            path: '/jsplumb',
            name: 'jsplumb',
            component: jsplumb
          }
        ]
    },
  ]
})



