<template>
    <div class="watch">
        <div>
            <input type="button" name="" value="+" @click="addLine" />
            <table>
                <tbody>
                    <tr v-if="test.testList" v-for="(item, index) in test.testList" :key="index">
                        <td>
                            <select v-model="item.selValue" @change="testChange($event,index)">
                                <option value="yingwen">English</option>
                                <option value="fantizhongwen">繁體中文</option>
                                <option value="jiantizhongwen">简体中文</option>
                            </select>
                        </td>
                        <td>
                            <input type="text" name="" v-model="item.inputValue"/>
                        </td>
                        <td>
                            <input type="button" name="" value="+" @click="addLine" />
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>
<script type="text/javascript">
    export default{
        name: 'watch',
        data() {
            return {
            }
        },
        props: ['test'],
        methods: {
            addLine() {
                this.test.testList.push({
                    selValue: 'yingwen',
                    inputValue: '输入框'
                })
            },
            testChange(e, idx) {
                // this.test.testList[idx].inputValue = e.target.value;
                this.$axios.get('/static/JSON/yesno.json')
                .then((response)=>{
                    this.test.testList[idx].inputValue = response.data[e.target.value];
                })
            }
        }
    }
</script>


