<template>
    <div class="watch">
        <p>
            Ask a yes/no question:
            <input v-model="question">
        </p>
        <p>{{answer}}</p>
        <!-- 测试 input 的 input 事件和 change 事件 -->
        <input type="text" name="" @input="inputEvent($event)">
        <select @change="selectChange($event)">
            <option value="1abc">1abc</option>
            <option value="2abc">2abc</option>
            <option value="3abc">3abc</option>
        </select>
        <test :test="testData"></test>
    </div>
</template>
<script type="text/javascript">
    export default{
        name: 'watch',
        data() {
            return {
                question: '',
                answer: 'I cannot give you an answer until you ask a question!',
                testData: {
                    testList: []
                }
            }
        },
        watch: {
            question: function(newQuestion, oldQuestion){
                this.answer = 'Waiting for you to stop typing...';
                this.debouncedGetAnswer();
            }
        },
        created(){
            this.debouncedGetAnswer = _.debounce(this.getAnswer, 500);
            // console.log(this.isPrimitive('aaa'));
        },
        methods: {
            getAnswer() {
                if(this.question.indexOf('?') === -1){
                    this.answer = 'Questions usually contain a question mark.';
                    return;
                }
                this.answer = 'Thinking...';
                var vm = this;
                this.$axios.get('/static/JSON/yesno.json')
                .then(function(response){
                    vm.answer = _.capitalize(response.data.answer);
                })
                .catch(function(error){
                    vm.answer = 'Error! Could not reach the API.' + error;
                })
            },
            inputEvent(e) {
                console.log(e.target.value);
            },
            selectChange(e) {
                console.log(e.target.value);
            }
        }
    }
</script>


