<template>
    <div>
        <el-form v-show="formLoaded" :rules="rules" ref="formTable" :model="form" label-width="20%">
            <table>
                <tbody>
                    <!-- <template v-if="!noEmail"> -->
                        <tr>
                            <td>name</td>
                            <td>
                                <el-form-item prop="name">
                                    <el-input v-model="form.name"></el-input>
                                </el-form-item>
                            </td>
                        </tr>
                        <tr>
                            <td>e-mail</td>
                            <td>
                                <el-form-item v-if="!noEmail" prop="email">
                                    <el-input v-model="form.email"></el-input>
                                </el-form-item>
                            </td>
                        </tr>
                    <!-- </template> -->
                    <tr>
                        <td>gender</td>
                        <td>
                            <el-form-item prop="gender">
                                <el-input v-model="form.gender"></el-input>
                            </el-form-item>
                        </td>
                    </tr>
                    <tr>
                        <td>hobbies</td>
                        <td>
                            <el-form-item>
                                <el-input v-model="form.hobbies"></el-input>
                            </el-form-item>
                        </td>
                    </tr>
                    <tr>
                        <td>school</td>
                        <td>
                            <el-form-item prop="school">
                                <el-input v-model="form.school"></el-input>
                            </el-form-item>
                        </td>
                    </tr>
                    <tr>
                        <td>tele</td>
                        <td>
                            <el-form-item prop="tele">
                                <el-input v-model="form.tele"></el-input>
                            </el-form-item>
                        </td>
                    </tr>
                </tbody>
            </table>
        </el-form>
        <el-form :rules="rules" ref="form" :model="form" label-width="20%">
            <el-form-item label="name" prop="name">
                <el-input v-model="form.name"></el-input>
            </el-form-item>
            <template v-if="!noEmail">
                <el-form-item label="e-mail" prop="email">
                    <el-input v-model="form.email"></el-input>
                </el-form-item>
            </template>
            <el-form-item label="gender" prop="gender">
                <el-input v-model="form.gender"></el-input>
            </el-form-item>
            <el-form-item label="school" prop="school">
                <el-input v-model="form.school"></el-input>
            </el-form-item>
            <el-form-item label="tele" prop="tele">
                <el-input v-model="form.tele"></el-input>
            </el-form-item>
            <el-form-item label="item1">
                <el-input v-model="form.item1"></el-input>
            </el-form-item>
            <el-form-item label="item2">
                <el-input v-model="form.item2"></el-input>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: 'elform',
        data() {
            return {
                noEmail: false,
                formLoaded: false,
                form: {
                    name: '',
                    email: '',
                    gender: '',
                    school: '',
                    tele: '',
                    hobbies: '',
                    item1: '',
                    item2: ''
                },
                rules: {
                    name: [
                        {required: true, message: 'name is required', trigger: ['blur', 'change']}
                    ],
                    email: [
                        {required: true, message: 'email is required', trigger: ['blur', 'change']}
                    ],
                    gender: [
                        {required: true, message: 'gender is required', trigger: ['blur', 'change']}
                    ],
                    school: [
                        {required: true, message: 'school is required', trigger: ['blur', 'change']}
                    ],
                    tele: [
                        {required: true, message: 'tele is required', trigger: ['blur', 'change']}
                    ],
                }
            }
        },
        created() {
            // setTimeout(()=>{
            //     this.noEmail = true;
            // }, 5000);
            if(this.$refs.formTable) {
                this.$refs.formTable.validate((valid)=>{});
            }
            if(this.$refs.form) {
                this.$refs.form.validate((valid)=>{});
            }
            this.$nextTick(()=>{
                this.$axios.get('/aaaa')
                .then(()=>{
                    console.log('succeed!');
                })
                .catch((err)=>{
                    // alert('err');
                    this.formLoaded = true;
                    this.form = {
                        name: '',
                        email: '',
                        gender: '',
                        school: '',
                        tele: '',
                        hobbies: ''
                    }
                    this.$set(this.form, 'name', '');
                    // this.form.name = '';
                    this.form.email = 'qqq@a.com';
                    this.form.genger = '';
                    this.form.school = 'bbb';
                    this.form.tele = '456789123';
                    this.form.hobbies = '';
                    this.noEmail = true;
                })
            })
        }
    }
</script>


