<template>
    <a-layout id="components-layout-demo-custom-trigger">
        <a-layout-sider :trigger="null" collapsible v-model="collapsed">
            <div class="logo">
                练习随记
            </div>
            <a-menu theme="dark" mode="inline" :defaultSelectedKeys="['1']">
                <a-menu-item key="1" @click="menuLink()">
                    <a-icon type="home" />
                    <span>首页</span>
                </a-menu-item>
                <a-menu-item key="2" @click="menuLink({ path: '/slider' })">
                    <a-icon type="user" />
                    <span>轮播图</span>
                </a-menu-item>
                <a-menu-item key="3" @click="menuLink({ path: '/watch' })">
                    <a-icon type="video-camera" />
                    <span>watch练习</span>
                </a-menu-item>
                <a-menu-item key="4" @click="menuLink({ path: '/align' })">
                    <a-icon type="align-center" />
                    <span>对齐方式</span>
                </a-menu-item>
                <a-menu-item key="5" @click="menuLink({ path: '/elform' })">
                    <a-icon type="form" />
                    <span>表单练习</span>
                </a-menu-item>
                <a-menu-item key="6" @click="menuLink({ path: '/jsplumb' })">
                    <a-icon type="branches" />
                    <span>试验流程图-jsplumb</span>
                </a-menu-item>
            </a-menu>
        </a-layout-sider>
        <a-layout>
            <a-layout-header style="background: #fff; padding: 0">
                <a-icon
                class="trigger"
                :type="collapsed ? 'menu-unfold' : 'menu-fold'"
                @click="()=> collapsed = !collapsed"
                />
            </a-layout-header>
            <a-layout-content :style="{ margin: '24px 16px', padding: '24px', background: '#fff', minHeight: '280px' }">
                <template v-if="indexPage">
                    <h1>Welcome to index page!</h1>
                    {{this.$store.state.count}}
                        <div>
                            <a-checkbox
                                :indeterminate="indeterminate"
                                :checked="checkAll" @change="checkAllChange">
                                Check all
                            </a-checkbox>
                        </div>
                        <br />
                        <a-checkbox-group :options="categoryList1" v-model="dataList" @change="showData" />
                    </div>
                    <a-textarea v-model.trim="msg" placeholder="随便输点什么" autosize />
                    <a-alert
                        message="Error!"
                        description="Something Happened."
                        type="error"
                        showIcon
                    />
                    <div class="imgList">
                        <div class="imgContainer">
                            <img src="/static/images/businessLicense.jpg" alt="">
                            <img src="/static/images/businessLicense.jpg" alt="">
                            <img src="/static/images/businessLicense.jpg" alt="">
                            <img src="/static/images/businessLicense.jpg" alt="">
                            <img src="/static/images/businessLicense.jpg" alt="">
                        </div>
                    </div>
                    <div class="jiankong">
                        <table>
                            <tbody>
                                <tr v-for="item in list">
                                    <td v-if="show">
                                        <el-select v-model="item.selValue" placeholder="请选择" @change="changeValue">
                                            <el-option
                                              v-for="item in options"
                                              :key="item.value"
                                              :label="item.label"
                                              :value="item.value">
                                            </el-option>
                                        </el-select>
                                        <el-select v-model="item.sel2Value" placeholder="请选择">
                                            <el-option
                                              v-for="item in categoryList[item.selValue]"
                                              :key="item.id"
                                              :label="item.name"
                                              :value="item.id">
                                            </el-option>
                                        </el-select>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                        <button @click="list.push({})">新增</button>
                    </div>
                </template>
                <template v-else>
                    <router-view></router-view>
                </template>
            </a-layout-content>
        </a-layout>
    </a-layout>
</template>
<script type="text/javascript">
    export default {
        name: 'index',
        data() {
            return {
                collapsed: false,
                msg: '',
                indexPage: true,
                indeterminate: true,
                checkAll: false,
                list: [],
                categoryList: {
                    '选项1': [{
                        id: 'b001200',
                        name: '小食品'
                    }],
                    '选项2': [{
                        id: 'b001201',
                        name: '水果'
                    }],
                    '选项3': [{
                        id: 'b001202',
                        name: '外卖配送'
                    }]
                },
                categoryList1: ['小食品', '水果', '外卖配送'],
                dataList: [],
                options: [{
                    value: '选项1',
                    label: '黄金糕'
                }, {
                    value: '选项2',
                    label: '双皮奶'
                }, {
                    value: '选项3',
                    label: '蚵仔煎'
                }, {
                    value: '选项4',
                    label: '龙须面'
                }, {
                    value: '选项5',
                    label: '北京烤鸭'
                }],
                value: '',
                show: true
            }
        },
        methods: {
            init() {
                // this.testPromise();
                this.$store.commit('increment');
            },
            testPromise() {
                console.log('1');
                setTimeout(function() {
                    console.log('2');
                    process.nextTick(function() {
                        console.log('3');
                    })
                    new Promise(function(resolve) {
                        console.log('4');
                        resolve();
                    }).then(function() {
                        console.log('5')
                    })
                })
                process.nextTick(function() {
                    console.log('6');
                })
                new Promise(function(resolve) {
                    console.log('7');
                    resolve();
                }).then(function() {
                    console.log('8')
                })

                setTimeout(function() {
                    console.log('9');
                    process.nextTick(function() {
                        console.log('10');
                    })
                    new Promise(function(resolve) {
                        console.log('11');
                        resolve();
                    }).then(function() {
                        console.log('12')
                    })
                })
                // 1 7 8 2 4 5 6 3 9 11 12 10
            },
            showData() {
                console.log(this.dataList);
                this.indeterminate = !!this.dataList.length && (this.dataList.length < this.categoryList1.length);
                this.checkAll = this.dataList.length === this.categoryList1.length;
            },
            inputEvent(e) {
                console.log(e.target.value);
            },
            menuLink(pathObj) {
                if(pathObj) {
                    this.$router.replace(pathObj);
                    this.indexPage = false;
                } else {
                    this.indexPage = true;
                }
            },
            checkAllChange(e) {
                this.dataList = e.target.checked ? this.categoryList1 : [];
                this.indeterminate =  false;
                this.checkAll = e.target.checked;
            },
            changeValue(e) {
                console.log(e);
            }
        },
        mounted() {
            this.init();
            // this.$loadlive2d("live2d", "./model.json");
        }
    }
    
</script>
<style lang="less" scoped>
    #components-layout-demo-custom-trigger {
        height: 100%;
        .trigger {
            font-size: 18px;
            line-height: 64px;
            padding: 0 24px;
            cursor: pointer;
            transition: color .3s;
            &:hover {
                color: #1890ff;
            }
        }
        .logo {
            // height: 32px;
            background: rgba(255,255,255,.2);
            margin: 16px;
            color: white;
            font-size: 20px;
            text-align: center;
            font-family: "黑体, Verdana, Geneva, Tahoma, sans-serif";
            padding: 10px 0;
        }
        .ant-layout {
            text-align: left;
        }
    }
    .imgList {
        overflow-x:  scroll;
    }
    .imgContainer {
        white-space: nowrap;
        img {
            width: 200px;
            height: auto;
        }
    }
</style>


