<template>
    <div>
        <div class="section">
            <div class="title">居中对齐</div>
            <div class="centerAlignText">这是一段div居中对齐文字</div>
            <div class="centerAlignElem">
                <span>水平居中对齐元素</span>
            </div>
        </div>
        <div class="section">
            <div class="title">垂直对齐</div>
            <div class="middleAlignText">这是一段div垂直对齐文字</div>
        </div>
        <input type="file" name="" @change="update">
        <div class="pdfContainer">
            <pdf
              v-for="i in numPages"
              :key="i"
              :page="i"
              :src="pdfUrl" style="width: 100%; height: auto;" @num-pages="pageCount=$event">
            </pdf>
        </div>
    </div>
</template>
<script type="text/javascript">
    import pdf from 'vue-pdf'
    export default {
        components: {
            pdf
        },
        name: 'alignWays',
        data() {
            return {
                pageCount: 0,
                pdfUrl: '',
                src: '/static/doc/test.pdf', // pdf文件地址
                numPages: 0
            }
        },
        methods: {
            update(e) {
                debugger;
                this.src = e.target.value;
                this.loadPdfHandler()
            },
            async loadPdfHandler () {
                //src为你服务器上存放pdf的路径
                this.pdfUrl = pdf.createLoadingTask(this.src);
                this.pdfUrl.promise.then(pdf => {
                    this.numPages = pdf.numPages
                })
            }
        },
        mounted() {
            
        }
    }
</script>
<style type="text/css" scoped>
    .pdfContainer {
        height: 500px;
        overflow: scroll;
    }
</style>


