import Vue from 'vue' // 引入 vue
import Vuex from 'vuex' // 引入 vuex
// 使用 vuex
Vue.use(Vuex);

// 创建 Vuex 实例
const store = new Vuex.Store({
    state: {
        count: 1
    },
    mutations: {
        increment (state){
            state.count++;
        }
    }
})

export default store // 导出 store


