<template>
    <div class="swiperWithThumbs">
        <!-- <router-link :to="{ path: '/' }">click to get back to index page</router-link> -->
        <swiper :options="swiperOptionTop" class="gallery-top" ref="swiperTop">
        <!-- slides -->
            <swiper-slide v-for="(slide, index) in swiperSlides" :key="index" :class="'slide-'+index">
                <img :src="slide"/>
            </swiper-slide>
            <!-- Optional controls -->
            <!-- <div class="swiper-pagination" slot="pagination"></div> -->
            <div class="swiper-button-next swiper-button-black" slot="button-next"></div>
            <div class="swiper-button-prev swiper-button-black" slot="button-prev"></div>
        </swiper>
        <swiper :options="swiperOptionThumbs" class="gallery-thumbs" ref="swiperThumbs">
            <swiper-slide v-for="(slide, index) in swiperSlides" :key="index" :class="'slide-'+index">
                <img :src="slide">
            </swiper-slide>
        </swiper>
    </div>
</template>

<script type="text/javascript">
    export default {
        name: 'slider',
        data() {
            return {
                swiperOptionTop: {
                    // swiper options/callbacks
                    spaceBetween: 10,
                    navigation: {
                        nextEl: '.swiper-button-next',
                        prevEl: '.swiper-button-prev'
                    }
                },
                swiperOptionThumbs: {
                    spaceBetween: 10,
                    centeredSlides: true,
                    slidesPerView: 'auto',
                    touchRatio: 0.2,
                    slideToClickedSlide: true
                },
                swiperSlides: [
                    require('../../static/images/businessLicense.jpg'),
                    require('../../static/images/businessLicense.jpg'),
                    require('../../static/images/businessLicense.jpg'),
                    require('../../static/images/businessLicense.jpg'),
                    require('../../static/images/businessLicense.jpg')
                ]
            }
        },
        methods: {
            
        },
        mounted() {
            this.$nextTick(() => {
                const swiperTop = this.$refs.swiperTop.swiper;
                const swiperThumbs = this.$refs.swiperThumbs.swiper;
                swiperTop.controller.control = swiperThumbs;
                swiperThumbs.controller.control = swiperTop;
            })
        }
    }
</script>
<style lang="less" scoped>
    .swiper-container{
        background-color: transparent;
    }
    .swiper-slide{
        background-size: cover;
        background-position: center;
    }
    .gallery-top{
        height: 50%!important;
        width: 80%;
        .swiper-slide{
            width: 50%;
        }
    }
    .gallery-thumbs{
        height: 20%!important;
        width: 40%;
        box-sizing: border-box;
        padding: 10px 0;
        .swiper-slide{
            width: 15%;
            height: 100%;
            opacity: 0.4;
            img{
                width: 100%;
            }
        }
        .swiper-slide-active{
            opacity: 1;
        }
    }
</style>


